<h3>属性值</h3>
<!-- ``` html
<bee-input
  :theme='String'
  :type='String'
  :icon='Object'
  :placeholder='String'
  :auto-focus='Boolean'
  :read-only='Boolean'
  :maxlength='Number'
  :reg='Function'
  :enter-event='Function'
  disabled
></bee-input>
``` -->
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>theme</td>
<td>按钮的类型</td>
<td>string</td>
<td>default,primary,success,error</td>
<td>default</td>
</tr>
<tr>
<td>type</td>
<td>输入框的类型</td>
<td>string</td>
<td>*</td>
<td>text</td>
</tr>
<tr>
<td>icon</td>
<td>要显示的图标</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位符</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>autoFocus</td>
<td>自动获取焦点</td>
<td>boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>readOnly</td>
<td>只读</td>
<td>boolean</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>maxlength</td>
<td>输入的最大长度</td>
<td>number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>reg</td>
<td>value的格式化函数</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>enterEvent</td>
<td>enter按键的回调函数</td>
<td>function</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用状态</td>
<td>boolean</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-comment">// icon的格式</span>

{
  <span class="hljs-attr">icon</span>: IconName,
  <span class="hljs-attr">position</span>: left|right
}
</code></pre>
</section></template>
<style>
.demo .ipt--wp {
  margin-right: 20px;
}
</style>
<script>
export default {
  data () {
    return {
      test1: null,
      test2: null,
      test3: null,
      mobile: null,
      readOnly: 'readOnly',
      icon: [{
        icon: 'search',
        position: 'left'
      }, {
        icon: 'mobile',
        position: 'left'
      }, {
        icon: 'correct',
        position: 'right'
      }, {
        icon: 'error',
        position: 'right'
      }]
    }
  },
  computed: {
    mobileReg () {
      return (value) => {
        return value ? /^(1|1[3-9]|1[3-9]\d{1,9})$/g.test(value) ? value : this.mobile : value
      }
    }
  },
  methods: {
    enterEvent () {
      console.log(`您输入的手机号为${this.mobile}`)
    }
  }
}
</script>